@use '../../assets/styles/variables.scss';

.header {
    padding: 18px 58px 22px;
    width: 100%;
    border-bottom: 5px solid #FFCAD4;
    .navigation {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        .nav-list {
            display: flex;
            gap: 20px;
            align-items:center;
            .search-panel {
                display: flex;
                gap: 8px;
                align-items: center;
                border:solid 1px #F5F5F5;
                padding: 6px 6px 7px ;
                input {
                    outline: none;
                    border:none;
                    background-color: transparent;
                    width: 193px;
                    padding: 0;
                }
                img{
                    width: 26px;
                }
            }
        }
    }
}