@import "../../assets/styles/variables.scss";

.quiz-section {
    width: 100vh;
    flex: 1;
    max-width: 320px;
    margin: 50px 58px 0 0;
    justify-content: flex-start;
    display: flex;
    flex-direction: column;
    &__pre-title {
        font-family: $mainFont;
        text-align: center;
        font-size: 1rem;
        line-height: 1rem;
        text-transform: uppercase;
        color:$colorGray;
        display: block;
    }
    &__title {
        font-family: $mainFont;
        text-align: center;
        font-size: 2rem;
        line-height: 2.2rem;
        color:$colorBlack;
        margin-top: 10px;
        font-weight: 700;
    }
    &__price-block {
        display: flex;
        align-self: center;
        gap: 15px;
        margin-top: 25px;
        strike,
        span{
            font-family: $mainFont;  
            font-size: 0.86rem;
            line-height: 1.2rem;
        }
        span{
            color:$colorPink;
        }
        strike{
            color:$colorBlack;
        }
    }
    &__text {
        font-family: $mainFont; 
        margin-top: 25px; 
        font-size: 1rem;
        line-height: 1.2rem;
        color:$colorBlack;
        max-width: 300px;
    }
    
   
}