@import './variables.scss';
@media screen and (max-width: 900px) {
    .quiz-section {
         max-width:100%;
         margin: 21px 0 50px 0;
         justify-content: center;
         align-items: center;
    }
    .main {
        flex-direction: column;
        align-items: center;
        gap:0px;
    }
    .image-block {
        margin: 20px 0 0;
    }
    .m-hide {
        display: none!important;
    }
    .m-show {
        display: block!important;
    }
    .image-block {
        width:100%;
        flex-direction: column;
        justify-content: center;
        align-items:center;
    }
    .feedback-button {
        position: absolute;
        top:12.5%;
    }
    .header{
        padding: 14px 14px 20px;
        .navigation {
            justify-content: center;
            .nav-list {
                width: 100%;
                justify-content: space-between;
                align-items: flex-end;
                &__item {
                    display: flex;
                }
                
            }
        }
    }
    
}
@media screen and (max-width: 345px ) {
    .feedback-button {
        position: absolute;
        top:29.5%;
    }
}