@import "../../assets/styles/variables.scss";

.feedback-button {
    text-transform: uppercase;
    background: #fff;
    position: fixed;
    display: block;
    top: 280px;
    right: -7px;
    transform: matrix(0, 1, -1, 0, 37, 0);
    padding: 9px 22px 11px 21px;
    border:solid 1px #B4B4B4;
    border-radius:3px;
    color:$colorBlack;
    font-family: $mainFont;
    width: 127px;
}