@use '../../assets//styles/variables.scss';

.image-block {
    display: flex;
    flex: 2;
    width: 100vh;
    max-width: 820px;
    margin: 30px 0 0 58px;
    &__content {
        display: flex;
        flex: 1;
        flex-wrap: wrap;
        align-content: stretch;
        width: 100%;
        gap: 20px;
        .image-item {
            flex-basis: 47.781%;
            img{
                width: 100%;
                height: 100%;
                object-fit: contain;
            }
    
        }
    }
}