@import "../../../assets/styles/variables.scss";

.quiz-block {
    margin-top: 40px;
    display: flex;
    flex-direction: column;
    &__progress {
        display: block;
        color:$colorBlack;
        font-family: $mainFont;
        font-size: 1rem;
        line-height: 1.3rem;
        font-weight: 700;
    }
    &__question {
        display: block;
        color:$colorBlack;
        font-family: $mainFont;
        font-size: 1rem;
        line-height: 1.3rem;
        padding-bottom: 10px;
        border-bottom: 2px solid #F5F5F5;
    }
}
.buttons-group {
    margin-top: 19px;
    display: flex;
    flex-direction: column;
    gap: 5px;
    a {
        width: 100%;
        border-radius: 3px;
        padding: 14px;
        border-color:transparent;
        cursor: url(../../../assets/image/cursor.png), auto;
        font-family: $mainFont;
        font-size: 0.86rem;
        line-height: 1rem;
        text-align: center;
        text-transform: uppercase;
        text-decoration: none;
    }
    &__positive {
        background: #27251F;
        color: #fff;
    }
    &__negative {
        background: #FFCAD4;
        color: #27251F;
    }
    &__positive:hover {
        background: #75736b;
        color: #fff;
        transition-duration: 0.5s;
    }
    &__negative:hover {
        background: #c48591;
        color: #27251F;
        transition-duration: 0.5s;
    }
}